import React, { useState } from 'react';
import * as Yup from 'yup';
import { Icon } from '@iconify/react';
import folderAddOutline from '@iconify/icons-eva/folder-add-outline';
import { useTheme } from '@mui/material/styles';
// material
import {
  TextField,
  Container,
  Stack,
  Typography,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  StepLabel,
  Box,
  Stepper,
  Step,
  StepContent,
  Paper
} from '@mui/material';
// Link as RouterLink,
import { useNavigate } from 'react-router-dom';
import { useFormik, Form, FormikProvider } from 'formik';
// components
import Page from '../components/Page';
import {
  ProductSort,
  ProductList,
  // AddProjectWidget,
  ProductFilterSidebar,
  TaskTable,
  createData
} from '../components/_dashboard/projects';
//
import PRODUCTS from '../_mocks_/products';

function BasicSetting({ formik, getFieldProps, touched, errors, values }) {
  return (
    <>
      <FormikProvider value={formik}>
        <Form autoComplete="off" noValidate>
          <Stack spacing={3}>
            <TextField
              fullWidth
              type="text"
              label="שם הפרוייקט"
              {...getFieldProps('projectName')}
              value={values.projectName}
              error={Boolean(touched.projectName && errors.projectName)}
              helperText={touched.projectName && errors.projectName}
            />

            <TextField
              id="filled-multiline-flexible"
              label="תיאור הפרוייקט"
              multiline
              minRows={3}
              maxRows={10}
              value={values.projectDescription}
              {...getFieldProps('projectDescription')}
              error={Boolean(touched.projectDescription && errors.projectDescription)}
              helperText={touched.projectDescription && errors.projectDescription}
            />
          </Stack>
        </Form>
      </FormikProvider>
    </>
  );
}

const rows = [
  // createData('Cupcake', 305, 3.7, 67, 4.3),
  // createData('Donut', 452, 25.0, 51, 4.9),
  // createData('Eclair', 262, 16.0, 24, 6.0),
  // createData('Frozen yoghurt', 159, 6.0, 24, 4.0),
  // createData('Gingerbread', 356, 16.0, 49, 3.9),
  // createData('Honeycomb', 408, 3.2, 87, 6.5),
  // createData('Ice cream sandwich', 237, 9.0, 37, 4.3),
  // createData('Jelly Bean', 375, 0.0, 94, 0.0),
  // createData('KitKat', 518, 26.0, 65, 7.0),
  // createData('Lollipop', 392, 0.2, 98, 0.0),
  // createData('Marshmallow', 318, 0, 81, 2.0),
  // createData('Nougat', 360, 19.0, 9, 37.0),
  // createData('Oreo', 437, 18.0, 63, 4.0)
];
const headCells = [
  {
    id: 'name',
    numeric: false,
    disablePadding: true,
    label: 'שם המשימה'
  },
  {
    id: 'calories',
    numeric: false,
    disablePadding: false,
    label: 'תיאור'
  },
  {
    id: 'fat',
    numeric: false,
    disablePadding: false,
    label: 'סטטוס'
  },
  {
    id: 'carbs',
    numeric: false,
    disablePadding: false,
    label: 'תאריך יצירה'
  },
  {
    id: 'protein',
    numeric: false,
    disablePadding: false,
    label: 'יוצר משימה'
  }
];

function TaskCreator({ headCells, rows }) {
  return (
    <div>
      <TaskTable headCells={headCells} rows={rows} />
    </div>
  );
}

export default function ProjectsPage() {
  const theme = useTheme();
  const navigate = useNavigate();
  const [openFilter, setOpenFilter] = useState(false);
  const [open, setOpen] = useState(false);
  const [activeStep, setActiveStep] = useState(0);
  // const [rowsData, setRowsData] = useState(rows);
  // const fullScreen = useMediaQuery(theme.breakpoints.down('md'));
  const handleNext = () => setActiveStep((prevActiveStep) => prevActiveStep + 1);
  const handleBack = () => setActiveStep((prevActiveStep) => prevActiveStep - 1);
  const handleReset = () => setActiveStep(0);
  const handleClickOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const handleOpenFilter = () => setOpenFilter(true);
  const handleCloseFilter = () => setOpenFilter(false);
  const handleResetFilter = () => {
    handleSubmit();
    resetForm();
  };
  const LoginSchema = Yup.object().shape({
    projectName: Yup.string()
      .min(3, 'קצר מידיי!')
      .max(50, 'ארוך מידיי!')
      .required('חובה להזין שם לפרוייקט'),
    projectDescription: Yup.string()
      .min(10, 'קצר מידיי!')
      .max(200, 'ארוך מידיי!')
      .required('חובה להזין תיאור לפרוייקט')
  });
  const formik = useFormik({
    initialValues: {
      projectName: '',
      projectDescription: '',
      remember: true
    },
    validationSchema: LoginSchema,
    onSubmit: () => {
      navigate('/dashboard', { replace: true });
      setOpenFilter(false);
    }
  });
  const { errors, touched, values, isSubmitting, handleSubmit, resetForm, getFieldProps } = formik;
  const ifSomeError = !(
    errors.projectName?.length > 0 ||
    errors.projectDescription?.length > 0 ||
    values.projectName?.length < 1
  );
  const steps = [
    {
      label: 'הגדרות בסיסיות',
      description: `בשלב הזה ניתן להגדיר את הפרטים הבסיסיים של הפרוייקט. רצוי להזין את כל הפרטים.`,
      component: (
        <BasicSetting
          formik={formik}
          getFieldProps={getFieldProps}
          touched={touched}
          errors={errors}
          values={values}
        />
      )
    },
    {
      label: 'יצירת משימות ושיוך אנשים',
      description:
        'בשלב הזה ניתן להגדיר משימות ולשייך משימות לאנשים. תמיד ניתן להוסיף משימות בהמשך.',
      component: <TaskCreator headCells={headCells} rows={rows} disabledBtn={rows.length > 0} />
    }
  ];
  return (
    <Page title="#כולא-לייק | לוח ניהול: פרוייקטים">
      <Container>
        <Stack direction="row" alignItems="center" justifyContent="space-between" mb={5}>
          <Typography variant="h4" gutterBottom>
            פרוייקטים
          </Typography>
          <Stack direction="row-reverse" alignItems="center" justifyContent="center">
            <Button
              sx={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'space-evenly'
              }}
              variant="contained"
              onClick={handleClickOpen}
              endIcon={<Icon icon={folderAddOutline} style={{ marginRight: '1rem' }} />}
            >
              הוספת פרוייקט
            </Button>
          </Stack>
        </Stack>
        <Dialog maxWidth="xl" open={open} onClose={handleClose}>
          <DialogTitle>יצירת פרוייקט חדש</DialogTitle>
          <DialogContent>
            {/* <DialogContentText>
              To subscribe to this website, please enter your email address here. We will send
              updates occasionally.
            </DialogContentText> */}
            {/* <ContentStyle> */}
            <Stepper activeStep={activeStep} orientation="vertical">
              {steps.map((step, index) => (
                <Step key={step.label}>
                  <StepLabel
                    optional={
                      index === 2 ? <Typography variant="caption">צעד אחרון</Typography> : null
                    }
                  >
                    {step.label}
                  </StepLabel>
                  <StepContent>
                    <Typography color={theme.palette.text.secondary}>{step.description}</Typography>
                    {step.component && <Box sx={{ width: '100%' }}>{step.component}</Box>}
                    <Box
                      sx={{
                        mb: 2,
                        display: 'flex',
                        direction: 'row',
                        justifyContent: 'left'
                      }}
                    >
                      <Stack direction="row-reverse">
                        <Button
                          disabled={!ifSomeError || (rows.length === 0 && index === 1)}
                          variant="outlined"
                          onClick={handleNext}
                          sx={{ mt: 1, mr: 1, color: theme.palette.success }}
                        >
                          {index === steps.length - 1 ? 'סיום' : 'קדימה'}
                        </Button>
                        <Button disabled={index === 0} onClick={handleBack} sx={{ mt: 1, mr: 1 }}>
                          חזרה
                        </Button>
                      </Stack>
                    </Box>
                  </StepContent>
                </Step>
              ))}
            </Stepper>
            {activeStep === steps.length && (
              <Paper square elevation={0} sx={{ p: 3 }}>
                <Typography>מעולה, סיימת את כל הצעדים. הפרוייקט מוכן!</Typography>
                <Button onClick={handleReset} sx={{ mt: 1, mr: 1 }}>
                  איפוס
                </Button>
              </Paper>
            )}
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose}>ביטול</Button>
            <Button onClick={handleClose} disabled={!ifSomeError || rows.length === 0}>
              שמירת פרוייקט
            </Button>
          </DialogActions>
        </Dialog>
        <Stack
          direction="row"
          flexWrap="wrap-reverse"
          alignItems="center"
          justifyContent="flex-end"
          sx={{ mb: 5 }}
        >
          <Stack direction="row" spacing={1} flexShrink={0} sx={{ my: 1 }}>
            <ProductFilterSidebar
              formik={formik}
              isOpenFilter={openFilter}
              onResetFilter={handleResetFilter}
              onOpenFilter={handleOpenFilter}
              onCloseFilter={handleCloseFilter}
            />
            <ProductSort />
          </Stack>
        </Stack>

        <ProductList products={PRODUCTS} />
      </Container>
    </Page>
  );
}
