import { useEffect } from 'react';
import { Icon } from '@iconify/react';
import googleFill from '@iconify/icons-eva/google-fill';
import facebookFill from '@iconify/icons-eva/facebook-fill';
import axios from 'axios';
import { Link as RouterLink, Route, Switch, useNavigate, Navigate } from 'react-router-dom';
// material
import { Stack, Button, Divider, Typography, Box, Snackbar, Slide } from '@mui/material';
import { useTheme } from '@mui/material/styles';
import LoadingButton from '@mui/lab/LoadingButton';
// GoogleButton
import { GoogleLogin } from 'react-google-login';
import { useAuth } from '../../context/UserProvider';
// ----------------------------------------------------------------------

export default function AuthSocial() {
  const {
    auth,
    userData,
    isAuthWithGoogle,
    isAuthWithFacebook,
    loading,
    error,
    signinWithGoogle,
    signinWithFacebook,
    signOutFromSocial
  } = useAuth();
  const theme = useTheme();
  return (
    <>
      <Stack
        sx={{
          display: 'flex',
          width: '100%',
          justifyContent: 'space-between',
          flexDirection: 'row',
          alignItems: 'center',
          mb: 3
        }}
        direction={['column', 'row']}
      >
        <LoadingButton
          loading={loading.google}
          sx={{ margin: '.5rem' }}
          fullWidth
          size="large"
          color="inherit"
          variant="outlined"
          onClick={!isAuthWithGoogle ? signinWithGoogle : signOutFromSocial}
        >
          <Icon icon={googleFill} color="#DF3E30" height={24} />
          <Typography variant="button" color={theme.typography.subtitle2}>
            {isAuthWithGoogle ? 'התנתקות מחשבון גוגל' : 'התחברות עם גוגל'}
          </Typography>
        </LoadingButton>
        <LoadingButton
          loading={loading.facebook}
          onClick={!isAuthWithFacebook ? signinWithFacebook : signOutFromSocial}
          sx={{ margin: '.5rem' }}
          fullWidth
          size="large"
          color="inherit"
          variant="outlined"
        >
          <Icon icon={facebookFill} color="#1877F2" height={24} />
          <Typography variant="button" color={theme.typography.subtitle2}>
            {isAuthWithFacebook ? 'התנתקות מחשבון פייסבוק' : 'התחברות עם פייסבוק'}
          </Typography>
        </LoadingButton>
      </Stack>
      <Snackbar
        open={!!error}
        autoHideDuration={3000}
        TransitionComponent={(props) => <Slide {...props} direction="up" />}
        message={error}
      />
      {/* <Divider sx={{ my: 3 }}>
        <Typography variant="body2" sx={{ color: 'text.secondary' }}>
          או
        </Typography>
      </Divider> */}
    </>
  );
}
