import React, { createContext, useContext, useEffect, useState } from 'react';
import {
  getAuth,
  signInWithPopup,
  GoogleAuthProvider,
  FacebookAuthProvider,
  signOut
} from 'firebase/auth';
import axios from 'axios';
import firebaseApp from '../auth/firebase';
// ------------------------------------------------

const api = axios.create({
  baseURL:
    process.env.NODE_ENV === 'production' ? 'https://kulalike.cf/api' : 'http://localhost:4000/api'
});

function AuthContext() {
  const googleProvider = new GoogleAuthProvider();
  const facebookProvider = new FacebookAuthProvider().addScope('email', 'public_profile');
  localStorage.setItem('isLoggedIn', false);
  const [user, setUser] = useState(null);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [isLoading, setIsLoading] = useState(true);

  const auth = getAuth(firebaseApp);
  const accessToken = auth.currentUser ? auth.currentUser.getIdToken() : null;
  const [isAuth, setIsAuth] = useState(false);

  useEffect(() => {
    const unsubscribe = getAuth().onAuthStateChanged(async (user) => {
      if (user) {
        const { uid, displayName, email, photoURL } = user;
        const userData = {
          uid,
          displayName,
          email,
          photoURL
        };
        setUser(userData);
        setIsLoggedIn(true);
        localStorage.setItem('isLoggedIn', true);
        localStorage.setItem('user', JSON.stringify(userData));
        setIsLoading(false);
      } else {
        setUser(null);
        setIsLoggedIn(false);
        localStorage.setItem('isLoggedIn', false);
        localStorage.removeItem('user');
        setIsLoading(false);
      }
    });
    return () => unsubscribe();
  }, []);

  auth.onAuthStateChanged((user) => {
    if (user) {
      api.defaults.headers.common.Authorization = `Bearer ${accessToken}`;
      setIsAuth(true);
    } else {
      delete api.defaults.headers.common.Authorization;
      setIsAuth(false);
    }
  });

  const [globalContext, setGlobalContext] = useState({
    userData: null,
    isAuth,
    isLoggedIn,
    isAuthWithGoogle: false,
    isAuthWithFacebook: false,
    loading: {
      facebook: false,
      google: false
    },
    error: null,
    signinWithGoogle,
    signinWithFacebook,
    signOutFromSocial
  });

  // useEffect(() => {
  //   setNewContext({
  //     userData: auth.currentUser,
  //     isAuthWithGoogle: auth.currentUser
  //       ? auth.currentUser.providerData[0].providerId === 'google.com'
  //       : false,
  //     isAuthWithFacebook: auth.currentUser
  //       ? auth.currentUser.providerData[0].providerId === 'facebook.com'
  //       : false,
  //     loading: {
  //       google: false,
  //       facebook: false
  //     },
  //     error: null
  //   });
  // }, [auth]);

  function hendleError(err) {
    if (err.code === 'auth/account-exists-with-different-credential') {
      return 'חשבון עם דואר אלקטרוני זהה כבר קיים במערכת';
    }
    if (err.code === 'auth/popup-closed-by-user') {
      return 'הפעלת החשבון בוטלה על ידי המשתמש';
    }
    if (err.code === 'auth/operation-not-allowed') {
      return 'הפעלת החשבון בוטלה על ידי המערכת';
    }
    if (err.code === 'auth/popup-blocked') {
      return 'הפעלת החשבון בוטלה על ידי המערכת';
    }
    return 'אירעה שגיאה בעת התחברות למערכת';
  }
  function setNewContext(newContext) {
    setGlobalContext((context) => ({
      ...context,
      ...newContext
    }));
  }
  async function signinWithGoogle() {
    try {
      setNewContext({ loading: { ...globalContext.loading, google: true }, error: null });
      const result = await signInWithPopup(auth, googleProvider);
      const { user } = result;
      const authToken = await user.getIdToken();
      const { data } = await api.post('/auth/login', { authToken });
      const { message } = data;
      if (message === 'User logged in') {
        setNewContext({
          isAuthWithGoogle: true,
          userData: user,
          loading: {
            facebook: false,
            google: false
          },
          error: null
        });
        localStorage.setItem('isLoggedIn', true);
      }
      setIsAuth(true);
    } catch (error) {
      setNewContext({
        loading: { facebook: false, google: false },
        isAuthWithGoogle: false,
        userData: null,
        error: hendleError(error)
      });
      localStorage.setItem('isLoggedIn', false);
      setIsAuth(false);
    }
  }
  async function signinWithFacebook() {
    try {
      setNewContext({ loading: { ...globalContext.loading, facebook: true }, error: null });
      const result = await signInWithPopup(auth, facebookProvider);
      const credential = FacebookAuthProvider.credentialFromResult(result);
      const token = credential.accessToken;
      const { user } = result;
      console.log('user logged with facebook', user);
      setNewContext({
        isAuthWithFacebook: true,
        userData: user,
        loading: { ...globalContext.loading, facebook: false },
        error: null
      });
      setIsAuth(true);
    } catch (error) {
      setNewContext({
        loading: { ...globalContext.loading, facebook: false },
        isAuthWithFacebook: false,
        userData: null,
        error: hendleError(error)
      });
      setIsAuth(false);
    }
  }
  async function signOutFromSocial() {
    try {
      setNewContext({ isLoading: true, error: null });
      await signOut(auth);
      setNewContext({
        isAuthWithGoogle: false,
        isAuthWithFacebook: false,
        userData: null,
        loading: { google: false, facebook: false },
        error: null
      });
      setIsAuth(false);
      localStorage.setItem('isLoggedIn', false);
    } catch (error) {
      setNewContext({
        isAuthWithGoogle: false,
        isAuthWithFacebook: false,
        userData: null,
        error: 'אירעה שגיאה בעת התנתקות ממערכת'
      });
    }
  }
  return globalContext;
}
const Context = createContext(null);
const { Provider } = Context;

export default function AuthProvider({ children }) {
  const globalContext = AuthContext();

  return <Provider value={globalContext}>{children}</Provider>;
}

AuthProvider.context = Context;

export const useAuth = () => useContext(Context);
