import axios from 'axios';

let url;

if (process.env.NODE_ENV === 'production') {
  url = 'https://kulalike.cf';
} else {
  url = 'http://localhost:4000';
}

export const apiWithCredentials = axios.create({
  baseURL: url,
  withCredentials: true
});

export const api = axios.create({
  baseURL: url
});
