import { useEffect, lazy, Suspense } from 'react';
import { Navigate, useRoutes, Routes, Route, Redirect, Switch } from 'react-router-dom';
import { Backdrop } from '@mui/material';
// layouts
import DashboardLayout from '../layouts/dashboard';
import LogoOnlyLayout from '../layouts/LogoOnlyLayout';
//
import Login from '../pages/Login';
import Register from '../pages/Register';
import DashboardApp from '../pages/DashboardApp';
import Projects from '../pages/Projects';
// import NewProject from './pages/NewProject';
import Blog from '../pages/Blog';
import User from '../pages/User';
import NotFound from '../pages/Page404';
import PrivateRoute from './PrivateRoute';
import PublicRoute from './PublicRoute';
import { useAuth } from '../context/UserProvider';
// ----------------------------------------------------------------------

export default function Router() {
  const { isAuthWithGoogle, isAuthWithFacebook } = useAuth();
  const user = JSON.parse(localStorage.getItem('user'));
  const isAuth = user && user.uid;
  return useRoutes([
    {
      path: '/',
      element: <DashboardLayout />,
      children: [
        {
          path: '/dashboard',
          element: isAuth ? <DashboardLayout /> : <Navigate to="/login" replace />,
          protected: true,
          children: [
            {
              path: '/dashboard/app',
              element: <DashboardApp />,
              protected: true
            },
            {
              path: '/dashboard/projects',
              element: <Projects />,
              protected: true
            },
            {
              path: '/dashboard/blog',
              element: <Blog />,
              protected: true
            },
            {
              path: '/dashboard/user',
              element: <User />,
              protected: true
            }
          ]
        }
      ]
    },
    {
      path: '/login',
      element: !isAuth ? <Login /> : <Navigate to="/dashboard" replace />
    },
    {
      path: '/404',
      element: <NotFound />
    },
    {
      path: '*',
      element: <Navigate to="/404" replace />
    }
  ]);
}
