import * as Yup from 'yup';
import { useState } from 'react';
import { Icon } from '@iconify/react';
import { useFormik, Form, FormikProvider } from 'formik';
import eyeFill from '@iconify/icons-eva/eye-fill';
import eyeOffFill from '@iconify/icons-eva/eye-off-fill';
import { useNavigate } from 'react-router-dom';
// material
import {
  Stack,
  TextField,
  IconButton,
  InputAdornment,
  MenuItem,
  Box,
  Typography
} from '@mui/material';
import { LoadingButton } from '@mui/lab';
import { UsersCard } from '../../../pages/User';
// ----------------------------------------------------------------------
const taskStatus = [
  {
    value: 'פעיל',
    color: 'green'
  },
  {
    value: 'מושהה',
    color: 'red'
  },
  {
    value: 'בוצע',
    color: 'blue'
  },
  {
    value: 'בוטל',
    color: 'black'
  }
];

export default function TaskForm() {
  const navigate = useNavigate();
  const [showPassword, setShowPassword] = useState(false);
  const [passFieldFocus, setPassFieldFocus] = useState(false);
  const TaskSchema = Yup.object().shape({
    taskName: Yup.string()
      .min(2, 'קצר מידיי!')
      .max(50, 'ארוך מידיי!')
      .required('חובה להזין שם משימה'),
    tastDescription: Yup.string().required('חובה להזין תיאור משימה'),
    taskStatus: Yup.string().required(),
    createDate: Yup.string().required(),
    taskCreator: Yup.string().required(),
    taskCreatorDetails: Yup.object()
      .shape({
        fullName: Yup.string().required(),
        email: Yup.string().required(),
        moreDetails: Yup.object().shape({
          phone: Yup.string(),
          linkedin: Yup.string()
        })
      })
      .required(),
    taskAssociates: Yup.array().of(
      Yup.object().shape({
        fullName: Yup.string(),
        email: Yup.string(),
        details: Yup.object().shape({
          phone: Yup.string(),
          linkedin: Yup.string()
        })
      })
    )
  });
  const [currency, setCurrency] = useState('פעיל');

  const handleChange = (event) => {
    setCurrency(event.target.value);
  };
  const formik = useFormik({
    initialValues: {
      taskName: '',
      tastDescription: '',
      taskStatus: '',
      createDate: '',
      taskCreator: '',
      taskCreatorDetails: {
        fullName: '',
        email: '',
        moreDetails: {
          phone: '',
          linkedin: ''
        }
      },
      taskAssociates: []
    },
    validationSchema: TaskSchema,
    onSubmit: () => {
      navigate('/dashboard', { replace: true });
    }
  });

  const { errors, touched, handleSubmit, isSubmitting, getFieldProps } = formik;

  return (
    <FormikProvider value={formik}>
      <Form autoComplete="off" noValidate onSubmit={handleSubmit}>
        <Stack spacing={3}>
          <Stack justifyContent="space-between" direction={{ xs: 'column', sm: 'row' }} spacing={2}>
            <TextField
              fullWidth
              label="שם המשימה"
              {...getFieldProps('taskName')}
              error={Boolean(touched.taskName && errors.taskName)}
              helperText={touched.taskName && errors.taskName}
            />
          </Stack>
          <TextField
            fullWidth
            rows={4}
            multiline
            label="תיאור"
            {...getFieldProps('tastDescription')}
            error={Boolean(touched.tastDescription && errors.tastDescription)}
            helperText={touched.tastDescription && errors.tastDescription}
          />
          <TextField
            id="outlined-select-currency"
            select
            label="סטטוס המשימה"
            value={currency}
            onChange={handleChange}
            // helperText="סטטוס המשימה. (ברירת המחדל: פעיל)"
          >
            {taskStatus.map(({ value, color }) => (
              <MenuItem key={value} value={value}>
                <div
                  style={{
                    width: '100%',
                    display: 'flex',
                    justifyContent: 'space-between',
                    padding: '0 0.25rem',
                    alignItems: 'center'
                  }}
                >
                  <span>{value}</span>
                  <span
                    style={{
                      backgroundColor: color,
                      width: '10px',
                      height: '10px',
                      borderRadius: '50%'
                    }}
                  />
                </div>
              </MenuItem>
            ))}
          </TextField>

          <Stack spacing={2}>
            <Box>
              <Typography variant="subtitle1">שיוך משתמשים</Typography>
            </Box>
            <UsersCard />
          </Stack>
          {/* <LoadingButton
            fullWidth
            size="large"
            type="submit"
            variant="contained"
            loading={isSubmitting}
          >
            התחברות
          </LoadingButton> */}
        </Stack>
      </Form>
    </FormikProvider>
  );
}
